import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  HostBinding,
  Input,
  Output
} from '@angular/core';

@Component({
  selector: 'sof-alert',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./alert.component.scss'],
  template: `
    <div class="alert alert-{{ type }}" role="alert">
      <sof-svg-icon
        class="alert-icon-type"
        [icon]="alertIcons[type]"
        size="20"
      ></sof-svg-icon>
      <div class="alert-content">
        <ng-content></ng-content>
      </div>
      <button class="btn btn-plain" (click)="onClose()">
        <sof-svg-icon
          *ngIf="isDismissible"
          class="alert-icon-dismiss"
          icon="icon-cross"
          size="12"
        ></sof-svg-icon>
      </button>
    </div>
  `
})
export class AlertComponent {
  /**
   * Type of alert that must be displayed. This has an impact on icons and colors.
   * Options: info, success, warning and danger.
   */
  @Input() type: 'info' | 'success' | 'warning' | 'danger' = 'info';

  /**
   * Whether or not the alert can be closed.
   */
  @Input() isDismissible = false;

  /**
   * Event that will inform when the alert is closed.
   */
  @Output() dismiss = new EventEmitter();

  @HostBinding('style.display') display: string;

  alertIcons = {
    ['info']: 'icon-info-circle',
    ['success']: 'icon-checkmark-circle',
    ['warning']: 'icon-warning',
    ['danger']: 'icon-cross-circle'
  };

  onClose(): void {
    this.display = 'none';
    this.dismiss.emit();
  }
}
